'use client';

import Button from '@/components/Button/Button';
import Container from '@/components/Container';
import useAuthStore from '@/app/store/authStore';
import { useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import AvatarUpload from '@/components/AvatarUpload';

type AuthFormProps = {
  mode: 'signin' | 'signup';
  email: string;
  password: string;
  error: string;
  onEmailChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onPasswordChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onSubmit: (e: React.FormEvent) => void;
  toggleMode: () => void;
};

type OnboardingFormProps = {
  name: string;
  onNameChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onSubmit: (e: React.FormEvent) => void;
};

// AuthForm component
const AuthForm = ({
  mode,
  email,
  password,
  error,
  onEmailChange,
  onPasswordChange,
  onSubmit,
  toggleMode,
}: AuthFormProps) => (
  <form className="flex flex-col gap-400" onSubmit={onSubmit}>
    <div className="flex flex-col gap-100">
      <h1 className="preset-3 font-bold text-neutral-900">
        {mode === 'signin' ? 'Welcome back' : 'Create an account'}
      </h1>
      <p className="preset-6 text-neutral-600">
        {mode === 'signin'
          ? ' Log in to continue tracking your mood and sleep.'
          : 'Join to track your daily mood and sleep with ease.'}
      </p>
    </div>
    <div className="flex flex-col gap-250">
      <div className="flex flex-col gap-100">
        <label htmlFor="email" className="preset-6 text-neutral-900">
          Email
        </label>
        <input
          type="email"
          id="email"
          value={email}
          onChange={onEmailChange}
          placeholder="name@example.com"
          className="preset-6 rounded-10 bg-neutral-0 border border-neutral-300 px-200 py-100 py-150 text-neutral-900"
        />
        {error && <p className="preset-6 text-red-700">{error}</p>}
      </div>
      <div className="flex flex-col gap-100">
        <label htmlFor="password" className="preset-6 text-neutral-900">
          Password
        </label>
        <input
          type="password"
          id="password"
          value={password}
          onChange={onPasswordChange}
          className="preset-6 rounded-10 bg-neutral-0 border border-neutral-300 px-200 py-100 py-150 text-neutral-900"
        />
      </div>
    </div>
    <Button className="w-full" label={mode === 'signin' ? 'Login' : 'Sign up'} onClick={() => {}} />
    <p className="preset-6 text-center text-neutral-600">
      {mode === 'signin' ? "Haven't got an account?" : 'Already have an account?'}
      <button
        type="button"
        className="preset-6 ml-2 cursor-pointer text-blue-600 hover:underline"
        onClick={toggleMode}
      >
        {mode === 'signin' ? 'Sign up' : 'Login'}
      </button>
    </p>
  </form>
);

// OnboardingForm component
const OnboardingForm = ({ name, onNameChange, onSubmit }: OnboardingFormProps) => (
  <form className="flex flex-col gap-400" onSubmit={onSubmit}>
    <div className="flex flex-col gap-100">
      <h1 className="preset-3 font-bold text-neutral-900">Personalize your experience</h1>
      <p className="preset-6 text-neutral-600">
        Add your name and a profile picture to make Mood yours.
      </p>
    </div>
    <div className="flex flex-col gap-100">
      <label htmlFor="name" className="preset-6 text-neutral-900">
        Name
      </label>
      <input
        type="text"
        id="name"
        value={name}
        placeholder="Jane Appleseed"
        onChange={onNameChange}
        className="preset-6 rounded-10 bg-neutral-0 border border-neutral-300 px-200 py-100 py-150 text-neutral-900"
      />
    </div>
    <AvatarUpload />
    <Button className="w-full" label="Start Tracking" onClick={() => {}} />
  </form>
);

const LoginPage = () => {
  const { login, signup } = useAuthStore();
  const [mode, setMode] = useState<'signin' | 'signup'>('signup');
  const router = useRouter();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [error, setError] = useState('');
  const [onboarding, setOnboarding] = useState(false);

  const toggleMode = useCallback(() => {
    setMode((prev) => (prev === 'signin' ? 'signup' : 'signin'));
    setError('');
  }, []);

  const handleAuthSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (mode === 'signin') {
      const success = await login(email, password);
      if (success) {
        router.push('/');
      } else {
        setError('Invalid credentials');
      }
    } else {
      if (email && password) {
        setOnboarding(true);
        setError('');
      } else {
        setError('Please fill in all fields');
      }
    }
  };

  const handleOnboardingSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const success = await signup(email, password, name);
    if (success) {
      router.push('/');
    } else {
      setError('Email already in use');
    }
  };

  return (
    <div className="flex h-screen flex-col items-center justify-start gap-400 px-200 py-1000">
      <img src="/images/logo.svg" alt="logo" className="h-500" />
      <Container className="max-w-[500px]">
        {!onboarding ? (
          <AuthForm
            mode={mode}
            email={email}
            password={password}
            error={error}
            onEmailChange={(e) => setEmail(e.target.value)}
            onPasswordChange={(e) => setPassword(e.target.value)}
            onSubmit={handleAuthSubmit}
            toggleMode={toggleMode}
          />
        ) : (
          <OnboardingForm
            name={name}
            onNameChange={(e) => setName(e.target.value)}
            onSubmit={handleOnboardingSubmit}
          />
        )}
      </Container>
    </div>
  );
};

export default LoginPage;
